/* eslint-disable prefer-destructuring */
import $ from 'jquery';
import closeModalTask from './closeModalTask';
import closeMainModal from './closeMainModal';
import isCorrectAnswer from './isCorrectAnswer';
import tasks from '../../data/tasks';
import cleanTaskBody from './cleanTaskBody';
import fillTaskBody from './fillTaskBody';

const generateExcessTask = (game) => {
  closeMainModal();
  cleanTaskBody();
  fillTaskBody();

  $('#modalTask .modal-title').html('Excess');
  $('#modalTask .question-area h4').html('hoose an exess image');
  $('.input-group').remove();
  $('#modalTask .modal-footer').empty();

  let n = Math.round(Math.random() * tasks.excess.length - 1);
  if (n < 0 || n >= tasks.excess.length) n = 0;

  const img1 = tasks.excess[n].img1;
  const img2 = tasks.excess[n].img2;
  const img3 = tasks.excess[n].img3;
  const img4 = tasks.excess[n].img4;
  const answer = tasks.excess[n].answer;

  $('#modalTask .modal-body').append(`<img src="${img1}" alt="img" width="200" heigth="150">`);
  $('#modalTask .modal-body').append(`<img src="${img2}" alt="img" width="200" heigth="150">`);
  $('#modalTask .modal-body').append(`<img src="${img3}" alt="img" width="200" heigth="150">`);
  $('#modalTask .modal-body').append(`<img src="${img4}" alt="img" width="200" heigth="150">`);

  const imgs = document.querySelectorAll('#modalTask .modal-body img');
  for (let i = 0; i < imgs.length; i++) {
    imgs[i].onclick = () => {
      closeModalTask();

      if (i + 1 === answer) {
        game.attackByHero();
        isCorrectAnswer(true);
      } else {
        game.attackByEnemy();
        isCorrectAnswer(false);
      }
    };
  }
};

export default generateExcessTask;
