import $ from 'jquery';
import SimpleTask from './SimpleTask';
import closeModalTask from './closeModalTask';
import closeMainModal from './closeMainModal';
import isCorrectAnswer from './isCorrectAnswer';

const generateSimpleTask = (game, type) => {
  const task = new SimpleTask(type);
  const answerArray = task.answer;

  closeMainModal();

  $('#answerInput').val('');

  document.querySelector('#submitSolution').onclick = () => {
    closeModalTask();

    const inputValue = $('#answerInput').val().toLowerCase();

    for (let i = 0; i < answerArray.length; i++) {
      if (inputValue === answerArray[i]) {
        game.attackByHero();
        isCorrectAnswer(true);
        break;
      } else if (i === answerArray.length - 1 && inputValue !== answerArray[i]) {
        game.attackByEnemy();
        isCorrectAnswer(false);
      }
    }
  };
};

export default generateSimpleTask;
