import $ from 'jquery';

class Player {
  constructor(name) {
    this.name = name;
    this.health = 100;
    this.score = 0;
    this.div = $('<div>');
    $('.player-name').html(this.name);
    $(this.div).addClass('player-zone');
    $(this.div).addClass('hero-stay-animation');
    $(this.div).appendTo($('.play-zone'));
    $(this.div).fadeIn(400);
  }

  idleAnimation() {
    $(this.div).addClass('hero-stay-animation');
  }

  shotAnimation() {
    $(this.div).fadeIn(400);

    const audio = new Audio('./audio/shot.wav');
    audio.play();

    if ($(this.div).hasClass('hero-stay-animation')) {
      $(this.div).removeClass('hero-stay-animation');
    }

    $(this.div).addClass('hero-shot-animation');

    setTimeout(() => {
      $(this.div).removeClass('hero-shot-animation');
      $(this.div).addClass('hero-stay-animation');
    }, 1000);
  }

  knifeAnimation() {
    $(this.div).fadeIn(400);

    if ($(this.div).hasClass('hero-stay-animation')) {
      $(this.div).removeClass('hero-stay-animation');
    }

    const audio = new Audio('./audio/knife.wav');
    audio.play();
    setTimeout(() => {
      audio.pause();
    }, 1000);

    $(this.div).addClass('hero-knife-animation');

    setTimeout(() => {
      $(this.div).removeClass('hero-knife-animation');
    }, 1000);
  }

  runAnimation() {
    $(this.div).fadeIn(400);

    if ($(this.div).hasClass('hero-stay-animation')) {
      $(this.div).removeClass('hero-stay-animation');
    }

    const audio = new Audio('./audio/run.wav');
    audio.play();

    $(this.div).addClass('hero-run-animation');

    setTimeout(() => {
      $(this.div).removeClass('hero-run-animation');
    }, 1000);
  }

  hurtAnimation() {
    $(this.div).fadeIn(400);

    if ($(this.div).hasClass('hero-stay-animation')) {
      $(this.div).removeClass('hero-stay-animation');
    }

    const audio = new Audio('./audio/hurt.wav');
    audio.play();
    audio.volume = 0.1;

    $(this.div).addClass('hero-hurt-animation');

    setTimeout(() => {
      $(this.div).removeClass('hero-hurt-animation');
      $(this.div).addClass('hero-stay-animation');
    }, 1000);
  }

  runAndKnifeAnimation() {
    this.runAnimation();
    setTimeout(() => {
      this.knifeAnimation();
    }, 1000);
    setTimeout(() => {
      $(this.div).fadeOut(400);
    }, 1600);
    setTimeout(() => {
      this.idleAnimation();
    }, 2000);
  }

  dieAnimation() {
    $(this.div).fadeIn(400);

    if ($(this.div).hasClass('hero-stay-animation')) {
      $(this.div).removeClass('hero-stay-animation');
    }

    $(this.div).addClass('hero-die-animation');

    const audio = new Audio('./audio/hurt.wav');
    audio.play();
    audio.volume = 0.1;

    setTimeout(() => {
      $(this.div).fadeOut(400);
    }, 600);

    setTimeout(() => {
      $(this.div).removeClass('hero-die-animation');
      $(this.div).remove();
    }, 1000);
  }
}

export default Player;
