import $ from 'jquery';
import KeyEvent from '../data/keyEvent';

const keySupport = () => {
  window.addEventListener('keydown', (event) => {
    if (event.keyCode === KeyEvent.ENTER && $('#startGame').length) {
      $('#startGame').click();
    }

    if (event.keyCode === KeyEvent.ENTER && !$('#startGame').length) {
      $('#submitSolution').click();
    }

    if (event.keyCode === KeyEvent.ENTER && $('#newGame').length) {
      $('#newGame').click();
    }

    if (event.keyCode === KeyEvent.SPACE && !$('#modalTask').is(':visible') && !$('#mainModal').is(':visible') && !$('#startGame').length) {
      $('#attack').click();
    }

    if (event.keyCode === KeyEvent.SHIFT) {
      $('input').focus();
    }

    if (event.keyCode === KeyEvent.CTRL) {
      $('input').blur();
    }

    if (event.keyCode === KeyEvent.RELOAD && !$('input').is(':focus')) {
      $('#reload-help-button').click();
    }

    if (event.keyCode === KeyEvent.BACK && !$('input').is(':focus')) {
      $('#home-help-button').click();
    }

    if (event.keyCode === KeyEvent.ARROW_LEFT && $('#carouselExampleControls').length && !$('input').is(':focus')) {
      $('.carousel-control-prev').click();
    }

    if (event.keyCode === KeyEvent.ARROW_RIGHT && $('#carouselExampleControls').length && !$('input').is(':focus')) {
      $('.carousel-control-next').click();
    }

    if (event.keyCode === KeyEvent.TASK_1 && $('#mainModal').is(':visible')) {
      $('#math-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_2 && $('#mainModal').is(':visible')) {
      $('#translate-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_3 && $('#mainModal').is(':visible')) {
      $('#dragdrop-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_4 && $('#mainModal').is(':visible')) {
      $('#audition-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_5 && $('#mainModal').is(':visible')) {
      $('#ballon-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_6 && $('#mainModal').is(':visible')) {
      $('#capitals-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_7 && $('#mainModal').is(':visible')) {
      $('#grammar-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_8 && $('#mainModal').is(':visible')) {
      $('#excess-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_9 && $('#mainModal').is(':visible')) {
      $('#moreless-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_10 && $('#mainModal').is(':visible')) {
      $('#puzzle-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_11 && $('#mainModal').is(':visible')) {
      $('#antonym-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_12 && $('#mainModal').is(':visible')) {
      $('#compare-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_13 && $('#mainModal').is(':visible')) {
      $('#definition-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_14 && $('#mainModal').is(':visible')) {
      $('#sequence-btn').click();
    }

    if (event.keyCode === KeyEvent.TASK_15 && $('#mainModal').is(':visible')) {
      $('#random-btn').click();
    }
  });
};

export default keySupport;
