import $ from 'jquery';
import tasks from '../../data/tasks';
import cleanTaskBody from './cleanTaskBody';
import fillTaskBody from './fillTaskBody';

class SimpleTask {
  constructor(type) {
    this.data = tasks[type];
    this.number = Math.round(Math.random() * (this.data.length - 1));
    this.question = this.data[this.number].question;
    this.answer = this.data[this.number].answer;

    cleanTaskBody();
    fillTaskBody();
    $('#modalTask .modal-title').html(type.toUpperCase());
    $('.question-area h4').html(this.question);
  }
}

export default SimpleTask;
