/* eslint-disable prefer-destructuring */
import $ from 'jquery';
import 'jquery-ui';
import 'jquery-ui/ui/widgets/sortable';
import 'jquery-ui/ui/disable-selection';
import closeModalTask from './closeModalTask';
import closeMainModal from './closeMainModal';
import isCorrectAnswer from './isCorrectAnswer';
import tasks from '../../data/tasks';
import cleanTaskBody from './cleanTaskBody';
import fillTaskBody from './fillTaskBody';

const generateDragDropTask = (game) => {
  closeMainModal();
  cleanTaskBody();
  fillTaskBody();

  $('#modalTask .modal-title').html('Drag And Drop');
  $('#modalTask .question-area h4').html('Arrange the letters in the correct order');
  $('.input-group').remove();

  let n = Math.round(Math.random() * tasks.dragdrop.length - 1);
  if (n < 0 || n >= tasks.dragdrop.length) n = 0;
  const word = tasks.dragdrop[n].word;
  const answer = tasks.dragdrop[n].answer;

  const letters = word.split('');

  $('#modalTask .modal-body').append('<ul id="sortable"></ul>');
  for (let i = 0; i < word.length; i++) {
    $('#modalTask .modal-body ul').append(`<li class="ui-state-default">${letters[i]}</li>`);
  }

  $('#sortable').sortable();
  $('#sortable').disableSelection();

  document.querySelector('#submitSolution').onclick = () => {
    closeModalTask();

    const li = document.querySelectorAll('#modalTask .modal-body ul li');
    let userAnswer = '';
    for (let i = 0; i < li.length; i++) {
      userAnswer += li[i].innerHTML;
    }

    if (answer === userAnswer) {
      game.attackByHero();
      isCorrectAnswer(true);
    } else {
      game.attackByEnemy();
      isCorrectAnswer(false);
    }
  };
};

export default generateDragDropTask;
