/* eslint-disable prefer-destructuring */
import $ from 'jquery';
import closeModalTask from './closeModalTask';
import closeMainModal from './closeMainModal';
import isCorrectAnswer from './isCorrectAnswer';
import tasks from '../../data/tasks';
import cleanTaskBody from './cleanTaskBody';
import fillTaskBody from './fillTaskBody';

const generateDefinitionTask = (game) => {
  closeMainModal();
  cleanTaskBody();
  fillTaskBody();

  let n = Math.round(Math.random() * tasks.definition.length - 1);
  if (n < 0 || n >= tasks.definition.length) n = 0;

  const img = tasks.definition[n].img;
  const answerArray = tasks.definition[n].answer;

  $('#modalTask .modal-title').html('Definition');
  $('#modalTask .question-area h4').html('What it is ?');
  $('#modalTask .question-area').append(`<img src="${img}" alt="img" width="200" heigth="150">`);

  document.querySelector('#submitSolution').onclick = () => {
    closeModalTask();

    const inputValue = $('#answerInput').val().toLowerCase();

    for (let i = 0; i < answerArray.length; i++) {
      if (inputValue === answerArray[i]) {
        game.attackByHero();
        isCorrectAnswer(true);
        break;
      } else if (i === answerArray.length - 1 && inputValue !== answerArray[i]) {
        game.attackByEnemy();
        isCorrectAnswer(false);
      }
    }
  };
};

export default generateDefinitionTask;
