import $ from 'jquery';
import Game from './Game';
import showGameSection from './showGameSection';
import keySupport from './keySupport';

const startGamePage = () => {
  keySupport();

  $('#startGame').on('click', () => {
    const name = $('#usernameInput').val();
    const bgLocation = $('.carousel-item.active img').attr('src');

    // eslint-disable-next-line no-new
    new Game(name || 'Username', bgLocation);

    $('.start-section').fadeOut(600);

    setTimeout(() => {
      $('.start-section').remove();

      showGameSection();
    }, 1500);
  });
};

export default startGamePage;
