import $ from 'jquery';
import audioButtons from './audioButtons';
import Player from './Characters/Player';
import Enemy from './Characters/Enemy';
import showRecords from './showRecords';
import generateMathTask from './Tasks/generateMathTask';
import generateSimpleTask from './Tasks/generateSimpleTask';
import generateDragDropTask from './Tasks/generateDragDropTask';
import generateAudtiionTask from './Tasks/generateAuditionTask';
import generateExcessTask from './Tasks/generateExcessTask';
import generateCompareTask from './Tasks/generateCompareTask';
import generateDefinitionTask from './Tasks/generateDefinitionTask';

class Game {
  constructor(name, bgLocation) {
    this.player = new Player(name);

    const health = this.player.score * 20 + 100;
    this.enemy = new Enemy(health);

    $('.bg-game').css('background-image', `url(${bgLocation})`);

    $('.btn-task').on('click', (e) => {
      const type = e.target.innerHTML.toLowerCase();
      generateSimpleTask(this, type);
    });

    $('.btn-task-math').on('click', () => generateMathTask(this));

    $('.btn-task-dragdrop').on('click', () => generateDragDropTask(this));

    $('.btn-task-audition').on('click', () => generateAudtiionTask(this));

    $('.btn-task-excess').on('click', () => generateExcessTask(this));

    $('.btn-task-compare').on('click', () => generateCompareTask(this));

    $('.btn-task-definition').on('click', () => generateDefinitionTask(this));

    audioButtons();
  }

  attackByHero() {
    if (Math.random() > 0.35) this.player.shotAnimation();
    else this.player.runAndKnifeAnimation();

    this.enemy.hurtAnimation();
    this.enemy.health -= 40;

    if (this.enemy.health <= 0) {
      this.enemy.deathAnimation();
      setTimeout(() => {
        this.player.score += 1;
        $('.score-number').html(this.player.score);

        const health = this.player.score * 20 + 100;
        this.enemy = new Enemy(health);
        $('.enemy-helth-level').css('width', `${(this.enemy.health / this.enemy.startHealth) * 100}%`);
      }, 3000);
    }

    $('.enemy-helth-level').css('width', `${(this.enemy.health / this.enemy.startHealth) * 100}%`);
  }

  attackByEnemy() {
    this.enemy.attackAnimation();

    this.player.hurtAnimation();
    this.player.health -= 20;

    if (this.player.health <= 0) {
      setTimeout(() => {
        this.player.dieAnimation();
        this.gameOver();
      }, 1000);
    }

    $('.player-helth-level').css('width', `${this.player.health}%`);
  }

  gameOver() {
    localStorage.setItem(`player:${this.player.name}`, `${this.player.score}`);

    setTimeout(() => {
      $('.play-zone').remove();
      $('.header-game').fadeOut(400);
      $('.score-section').fadeOut(400);
      $('.attack-button').fadeOut(400);
      $('.help-buttons').fadeOut(400);
    }, 1000);

    setTimeout(() => {
      $('.header-game').remove();
      $('.score-section').remove();
      $('.attack-button').remove();
      $('.help-buttons').remove();
    }, 1400);

    showRecords();
  }
}

export default Game;
