import $ from 'jquery';
import { firstNames, secondNames, thirdNames } from '../../data/enemyNames';

class Enemy {
  constructor(health) {
    this.name = `${firstNames[Math.round(Math.random() * (firstNames.length - 1))]} ${secondNames[Math.round(Math.random() * (secondNames.length - 1))]} ${thirdNames[Math.round(Math.random() * (thirdNames.length - 1))]}`;
    this.health = health;
    this.startHealth = health;
    this.div = $('<div>');
    $('.enemy-name').html(this.name);
    $(this.div).addClass('enemy-zone');
    $(this.div).fadeOut(0);
    $(this.div).appendTo($('.play-zone'));
    $(this.div).append(`<img class="enemy-head" src="img/enemy/${Math.round(Math.random() * 2 + 1)}/head.png" alt="head" height="170" width="150">`);
    $(this.div).append(`<img class="enemy-neck" src="img/enemy/${1}/neck.png" alt="neck" height="20" width="40">`);
    $(this.div).append(`<img class="enemy-body" src="img/enemy/${Math.round(Math.random() * 2 + 1)}/body.png" alt="body" height="120" width="90">`);
    $(this.div).append(`<img class="enemy-left-shoulder" src="img/enemy/${Math.round(Math.random() * 4 + 1)}/left_shoulder.png" alt="left_shoulder" height="90" width="40">`);
    $(this.div).append(`<img class="enemy-left-forearm" src="img/enemy/${1}/left_forearm.png" alt="left_forearm" height="70" width="20">`);
    $(this.div).append(`<img class="enemy-left-arm" src="img/enemy/${1}/left_arm1.png" alt="left_arm" height="40" width="40">`);
    $(this.div).append(`<img class="enemy-right-shoulder" src="img/enemy/${Math.round(Math.random() * 4 + 1)}/right_shoulder.png" alt="right_shoulder" height="90" width="40">`);
    $(this.div).append(`<img class="enemy-right-forearm" src="img/enemy/${1}/right_forearm.png" alt="right_forearm" height="70" width="20">`);
    $(this.div).append(`<img class="enemy-right-arm" src="img/enemy/${1}/right_arm1.png" alt="right_arm" height="40" width="40">`);
    $(this.div).append(`<img class="enemy-pants" src="img/enemy/${1}/pants.png" alt="pants" height="40" width="90">`);
    $(this.div).append(`<img class="enemy-left-hip" src="img/enemy/${1}/left_hip.png" alt="left_hip" height="80" width="40">`);
    $(this.div).append(`<img class="enemy-left-leg" src="img/enemy/${1}/left_leg.png" alt="left_leg" height="80" width="50">`);
    $(this.div).append(`<img class="enemy-right-hip" src="img/enemy/${1}/right_hip.png" alt="right_hip" height="80" width="40">`);
    $(this.div).append(`<img class="enemy-right-leg" src="img/enemy/${1}/right_leg.png" alt="right_leg" height="70" width="50">`);
    $(this.div).append(`<img class="enemy-weapon" src="img/enemy/${Math.round(Math.random() * 4 + 1)}/weapon.png" alt="body" height="80" width="160">`);
    $(this.div).append('<img class="enemy-blood" src="img/enemy/blood.png" alt="body" height="200" width="400">');
    $(this.div).fadeIn(1000);
  }

  attackAnimation() {
    if (!document.querySelector('.enemy-weapon-shot')) $(this.div).append('<div class="enemy-weapon-shot">');
    else $('.enemy-weapon-shot').css('display', 'block');

    const audio = new Audio('./audio/shot.wav');
    audio.play();

    setTimeout(() => {
      $('.enemy-weapon-shot').css('display', 'none');
    }, 1000);
  }

  hurtAnimation() {
    $(this.div).addClass('enemy-hurt');

    const audio = new Audio('./audio/hurt.wav');
    audio.play();
    audio.volume = 0.1;

    setTimeout(() => {
      $('.enemy-blood').fadeIn(400);
    }, 1000);

    setTimeout(() => {
      $('.enemy-blood').fadeOut(400);
      $(this.div).removeClass('enemy-hurt');
    }, 3000);
  }

  deathAnimation() {
    this.hurtAnimation();

    const audio = new Audio('./audio/hurt.wav');
    audio.play();
    audio.volume = 0.1;

    setTimeout(() => {
      $(this.div).fadeOut(400);
    }, 2600);

    setTimeout(() => {
      $(this.div).remove();
    }, 3000);
  }
}

export default Enemy;
