import $ from 'jquery';
import closeModalTask from './closeModalTask';
import closeMainModal from './closeMainModal';
import isCorrectAnswer from './isCorrectAnswer';
import tasks from '../../data/tasks';
import cleanTaskBody from './cleanTaskBody';
import fillTaskBody from './fillTaskBody';

const generateAuditionTask = (game) => {
  closeMainModal();
  cleanTaskBody();
  fillTaskBody();

  $('#modalTask .modal-title').html('Audition');
  $('#modalTask .question-area h4').html('Listen the recording and write the word');

  let n = Math.round(Math.random() * tasks.audition.length - 1);
  if (n < 0 || n >= tasks.audition.length) n = 0;
  const word = new Audio(tasks.audition[n].audio);
  word.play();
  // eslint-disable-next-line prefer-destructuring
  const answer = tasks.audition[n].answer;

  $('#modalTask .question-area').append('<button id="audition-play" type="button" class="btn btn-danger"><i class="fas fa-volume-up"></i></button>');
  $('#audition-play').on('click', () => word.play());

  document.querySelector('#submitSolution').onclick = () => {
    closeModalTask();

    if ($('#answerInput').val().toLocaleLowerCase() === answer) {
      game.attackByHero();
      isCorrectAnswer(true);
    } else {
      game.attackByEnemy();
      isCorrectAnswer(false);
    }
  };
};

export default generateAuditionTask;
