import $ from 'jquery';

const showRecords = () => {
  const records = [];
  for (let i = 0; i < localStorage.length; i++) {
    if (localStorage.key(i).includes('player:')) {
      records.push({
        name: localStorage.key(i).slice(7),
        score: localStorage.getItem(localStorage.key(i)),
      });
    }
  }

  records.sort((a, b) => b.score - a.score);
  if (records.length > 10) records.length = 10;

  $('.records-section').append('<table class="table"></table>');
  $('.records-section .table').append('<thead></thead>');
  $('.records-section .table').append('<tbody></tbody>');
  $('.records-section .table thead').append('<tr></tr>');
  $('.records-section .table thead tr').append('<th scope="col">#</th>');
  $('.records-section .table thead tr').append('<th scope="col">Name</th>');
  $('.records-section .table thead tr').append('<th scope="col">Score</th>');
  $('.records-section .table tbody').append('<tr></tr>');

  for (let i = 0; i < records.length; i++) {
    $('.records-section .table tbody').append('<tr></tr>');
    $(`.records-section .table tbody tr:nth-child(${i + 1})`).append(`<th scope="row">${i + 1}</th>`);
    $(`.records-section .table tbody tr:nth-child(${i + 1})`).append(`<td>${records[i].name}</td>`);
    $(`.records-section .table tbody tr:nth-child(${i + 1})`).append(`<td>${records[i].score}</td>`);
  }

  setTimeout(() => {
    $('.records-section').fadeIn(400);
    $('.new-game-btn-container').fadeIn(400);
    $('#newGame').on('click', () => {
      window.location.reload();
    });
  }, 1500);
};

export default showRecords;
