import Swal from 'sweetalert2';

const isCorrectAnswer = (isCorrect) => {
  const toast = Swal.mixin({
    toast: true,
    showConfirmButton: false,
    timer: 2000,
  });

  const rightAnswer = {
    type: 'success',
    title: 'Answer is correct',
  };

  const wrongAnswer = {
    type: 'error',
    title: 'Wrong answer',
  };

  if (isCorrect) toast(rightAnswer);
  else toast(wrongAnswer);
};

export default isCorrectAnswer;
