import $ from 'jquery';

const fillTaskBody = () => {
  $('#modalTask .modal-body').append('<div class="question-area"></div>');
  $('#modalTask .modal-body .question-area').append('<h4>Question</h4>');

  $('#modalTask .modal-body').append('<div class="input-group input-group-sm mb-3"></div>');
  $('#modalTask .modal-body .input-group').append('<div class="input-group-prepend"></div>');
  $('#modalTask .modal-body .input-group .input-group-prepend').append('<span class="input-group-text" id="inputGroup-sizing-sm">Answer:</span>');
  $('#modalTask .modal-body .input-group').append('<input id="answerInput" type="text" class="form-control">');

  $('#modalTask .modal-footer').append('<button id="submitSolution" type="button" class="btn btn-dark">SUBMIT</button>');
};

export default fillTaskBody;
