import $ from 'jquery';
import closeModalTask from './closeModalTask';
import closeMainModal from './closeMainModal';
import isCorrectAnswer from './isCorrectAnswer';
import cleanTaskBody from './cleanTaskBody';
import fillTaskBody from './fillTaskBody';

const generateMathTask = (game) => {
  closeMainModal();
  cleanTaskBody();
  fillTaskBody();

  const a = Math.round(Math.random() * 10);
  const b = Math.round(Math.random() * 10);
  const operation = ['+', '-', '*'][Math.round(Math.random() * 2)];
  const question = `${a} ${operation} ${b}`;
  // eslint-disable-next-line no-eval
  const answer = `${eval(question)}`;

  $('#modalTask .modal-title').html('MATH');
  $('#modalTask .question-area h4').html(`How many will be ${question} ?`);
  $('#answerInput').val('');

  document.querySelector('#submitSolution').onclick = () => {
    closeModalTask();

    if ($('#answerInput').val() === answer) {
      game.attackByHero();
      isCorrectAnswer(true);
    } else {
      game.attackByEnemy();
      isCorrectAnswer(false);
    }
  };
};

export default generateMathTask;
